<template>
<div class="fx-questionnaire">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-tips">低保额问卷</div>
    <div class="fx-content" :style="{height:clientHeight+'px'}">
        <tree-answer v-for="(value,index) in questionList" :data="value" :parentIndex="index" :key="index" @change_data = "change_data"></tree-answer>
    </div>
    <div class="fx-btns">
        <a class="nextBtn" @click="goNext">下一步</a>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import treeAnswer from "@/components/treeAnswer";
import { DatetimePicker,Indicator } from 'mint-ui';
var axios = require('axios');
export default {
    name:'questionnaire',
    data(){
        return{
            orderId:null,
            timeQuestionIndex:null,
            isClicked:false,
            dateValue:'',
            showDate:'',
            clientHeight:0,
            clientHeight1:0,
            headerTitle:'健康告知',
            questionList:[],
            answerList:{},//传给后台的数据
        }
    },
    created() {

    },
    methods: {
        change_data(data){
            //console.log("aa");
            console.log("this.questionList=====",this.questionList);
            //重排一遍 sortIndex
            let tmpArr = this.dealChangeData(this.questionList);
            //this.questionList = tmpArr;
        },
        //处理变化的数据
        dealChangeData(data){
            let THAT = this;
            var m = 1,count = 1;
            let toSortIndex = function(arr){
                count++
                //console.log(count);
                arr.map((item)=>{
                    if(item.show){
                        if(item.configType !== 5){
                            //item.sortIndex = m++;
                            THAT.$set(item,'sortIndex',m++)
                        }
                        if(item.require instanceof Array && item.require.length>0){
                            toSortIndex(item.require);
                        }
                    }
                })
            }
            var tmpArr = toSortIndex(data);
        },
        //从上往下校验所有的问题是否有answer
        checkAnswer(data){
            var flag = true;
            let THAT= this;
            var answerList = {};
            let findAnswer = function(arr){
                arr.map(item=>{
                    if(flag){
                        console.log("item=====",item);
                        if(item.answer instanceof Array){//多选情况下
                            console.log("item.answer====",item.answer);
                            console.log("item",item);
                            if(item.answer.length === 0){
                                flag = false
                                return THAT.$popup.alert('请回答--'+item.content);//这里弹出用户没有填写的问题，提示作用
                            }
                            else{
                                answerList[item.code] = item.answer;
                                findAnswer(item.require);
                            }
                        }
                        else{
                            //去掉所有空格 判断下 
                            let str = item.answer && item.answer.split(" ").join("").replace(regPa,'');
                            //去掉特殊字符
                            let regPa = /[@#$%^&]/g;
                            if(item.show && str !== "" && item.configType !== 5){
                                answerList[item.code] = item.answer.replace(regPa,'');
                            }
                            if(item.show && str !== "" && item.require instanceof Array && item.require.length>0){
                                findAnswer(item.require);
                            }
                            else if(item.show && str === ""){
                                flag = false
                                return THAT.$popup.alert('请回答--'+item.content);//这里弹出用户没有填写的问题，提示作用
                            }
                        }
                    }
                })
            }
            let tmp = findAnswer(data);
            console.log("answerList",answerList);
            THAT.answerList = answerList;
            return flag;
        },
        //进入智能审核
        goNext(event){
            console.log(event.target);
            event.target.focus();
            console.log("this.questionList",this.questionList);
            //校验
            let flag = this.checkAnswer(this.questionList);
            console.log("flag",flag);
            let THAT = this;
            if(flag){//数据传给后台
                let params = THAT.answerList;
                //console.log("params",params);
                sessionStorage.setItem("QUESTION_PARAMS",JSON.stringify(params));
                setTimeout(()=>{
                    THAT.$router.push({path:'/fuxing/upload1'});
                },100)
            }
        },
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //获取所有的问题 和答案选项
        getQuestionList(){
            let THAT = this;
            let data = [{"code":"Q00000001","id":1,"configType":1,"parentId":0,"content":"不符合健康告知的内容（疾病名称/异常状况）"},{"code":" ","id":4,"configType":5,"parentId":3,"content":"从未发作"},{"code":"Q000000014","id":1571382933510,"configType":7,"parentId":5,"content":"您首次发病(或出现异常)时间?"},{"code":"0","id":1571383568919,"configType":5,"parentId":1571382976090,"content":"每天"},{"code":" Q000000019","id":1573696867876,"configType":1,"parentId":1573543333208,"content":"请录入具体药物名称、服用剂量、服用频率"},{"code":"Q000000018 ","id":1573711686696,"configType":1,"parentId":1573543898707,"content":"请录入具体手术名称、术后病理结果"},{"code":" Q000000020","id":1573696827701,"configType":1,"parentId":1573543560264,"content":"请录入物理治疗的具体描述"},{"code":" Q000000019","id":1573711831862,"configType":1,"parentId":1573543964108,"content":"请录入具体药物名称、服用剂量、服用频率"},{"code":" Q000000021","id":1573711798848,"configType":1,"parentId":1573544068785,"content":"请录入其他治疗的具体描述"},{"code":" Q000000018","id":1573696782266,"configType":1,"parentId":1573543275015,"content":"请录入具体手术名称、术后病理结果"},{"code":" Q000000021","id":1573696907204,"configType":1,"parentId":1573543636976,"content":"请录入其他治疗的具体描述"},{"code":"0","id":1571383525577,"configType":5,"parentId":1571382957840,"content":"不超过一小时"},{"code":" Q000000020","id":1573711741136,"configType":1,"parentId":1573544018330,"content":"请录入物理治疗的具体描述"},{"code":"0","id":1571383097592,"configType":5,"parentId":1571383017872,"content":"是"},{"code":"Q00000002","id":2,"configType":1,"parentId":0,"content":"您的主要症状/状况，以及严重程度"},{"code":" ","id":5,"configType":5,"parentId":3,"content":"有发作"},{"code":"Q000000015","id":1571382957840,"configType":3,"parentId":5,"content":"每次发病持续时间"},{"code":"Q00000004","id":1571383192336,"configType":1,"parentId":1571383097592,"content":"其他关联疾病，比如原发病、并发症或后遗症说明？"},{"code":"0","id":1571383164985,"configType":5,"parentId":1571383017872,"content":"否"},{"code":"0","id":1571383536471,"configType":5,"parentId":1571382957840,"content":"不超过一天"},{"code":"0","id":1571383375966,"configType":5,"parentId":1571383209314,"content":"未曾接受过治疗"},{"code":"0","id":1571383578984,"configType":5,"parentId":1571382976090,"content":"每周"},{"code":"Q00000006","id":1571383221888,"configType":5,"parentId":1571383209314,"content":"正在接受"},{"code":"0","id":1571383232743,"configType":5,"parentId":1571383209314,"content":"曾接受过治疗但已停止"},{"code":"Q00000003","id":3,"configType":3,"parentId":0,"content":"您的疾病发作情况"},{"code":"0","id":1571383590160,"configType":5,"parentId":1571382976090,"content":"每月"},{"code":"Q00000006","id":1573543734810,"configType":4,"parentId":1571383232743,"content":"治疗方法？"},{"code":"Q000000016","id":1571382976090,"configType":3,"parentId":5,"content":"平均发作频率"},{"code":"Q00000006","id":1573543055329,"configType":4,"parentId":1571383221888,"content":"治疗方法？（可多选）"},{"code":"0","id":1571383546103,"configType":5,"parentId":1571382957840,"content":"不超过一周"},{"code":" ","id":1573543275015,"configType":5,"parentId":1573543055329,"content":"手术治疗"},{"code":"Q00000007","id":1573544189193,"configType":3,"parentId":1571383232743,"content":"治疗结果"},{"code":"0","id":1571383554591,"configType":5,"parentId":1571382957840,"content":"一周以上"},{"code":"Q00000008","id":1573544246048,"configType":7,"parentId":1571383232743,"content":"最后一次治疗时间"},{"code":"0","id":1571383600447,"configType":5,"parentId":1571382976090,"content":"每年"},{"code":"Q00000008","id":1573639633219,"configType":7,"parentId":1571383221888,"content":"最后一次治疗时间"},{"code":" ","id":1573543560264,"configType":5,"parentId":1573543055329,"content":"物理治疗"},{"code":" ","id":1573543898707,"configType":5,"parentId":1573543734810,"content":"手术治疗"},{"code":" ","id":1573543333208,"configType":5,"parentId":1573543055329,"content":"药物治疗"},{"code":"Q00000004","id":1571383017872,"configType":3,"parentId":0,"content":"是否有其他关联疾病，比如原发病、并发症或后遗症?"},{"code":"Q000000020","id":1573544018330,"configType":5,"parentId":1573543734810,"content":"物理治疗"},{"code":"Q000000019","id":1573543964108,"configType":5,"parentId":1573543734810,"content":"药物治疗"},{"code":"Q000000017","id":1571382987214,"configType":7,"parentId":5,"content":"最近一次发作时间"},{"code":" ","id":1573543636976,"configType":5,"parentId":1573543055329,"content":"其他治疗"},{"code":"0","id":1571383609351,"configType":5,"parentId":1571382976090,"content":"1-2次"},{"code":"0","id":1573544206123,"configType":5,"parentId":1573544189193,"content":"治愈，无不良症状，无复发"},{"code":"0","id":1573544218704,"configType":5,"parentId":1573544189193,"content":"症状缓解"},{"code":"Q00000005","id":1571383209314,"configType":3,"parentId":0,"content":"您是否接受过治疗？"},{"code":"0","id":1573544231554,"configType":5,"parentId":1573544189193,"content":"未治愈，治疗无效果，症状无缓解或加重"},{"code":"Q000000016","id":1571383662000,"configType":1,"parentId":1571383632657,"content":"其他平均发作频率说明"},{"code":"Q000000021","id":1573544068785,"configType":5,"parentId":1573543734810,"content":"其他治疗"},{"code":"Q00000009","id":1571383389576,"configType":1,"parentId":0,"content":"补充说明"},{"code":"0","id":1571383619496,"configType":5,"parentId":1571382976090,"content":"3次以上"},{"code":"0","id":1571383632657,"configType":5,"parentId":1571382976090,"content":"其他"}]
            THAT.dealData(data);
            // Indicator.open({
            //     text: '加载中...',
            //     spinnerType: 'fading-circle'
            // });
            // THAT.$http.getHttp({
            //     name:'fxFormData',
            //     params:{},
            //     orderId:THAT.orderId,
            //     type:'get'
            // },
            // res => {
            //     Indicator.close();
            //     console.log("res",JSON.stringify(res.data));
            //     THAT.dealData(res.data);
            // })
        },
        //渲染树节点 单选

        //totree
        toTreeData (data) {
            let parents = data.filter(value => value.parentId == '0');
            let children = data.filter(value => value.parentId !== '0');
            console.log("parents",parents);
            parents.map((item,i)=>{
                item.show = true;
                item.sortIndex = i+1;
            })
            console.log("children",children);
            let translator = (parents, children) => {
                parents.forEach((parent) => {
                    children.forEach((current, index) => {
                        if (current.parentId === parent.id) {
                            let temp = JSON.parse(JSON.stringify(children))
                            temp.splice(index, 1)
                            translator([current], temp)
                            typeof parent.require !== 'undefined' ? parent.require.push(current) : parent.require = [current]
                        }
                    })
                })
            }
            translator(parents,children);
            return parents
        },
        //对数据进行处理
        //configType 1输入 2选择 3单选 4多选 5选项 6上传
        dealData(arr){
            //问题格式处理
            arr.map((item)=>{
                if(item.configType === 1){
                    item["type"] = "text",
                    item["require"] = "字数200字以内",
                    item["answer"] = ""
                }
                else if(item.configType === 2){
                    item["require"] = [],
                    item["answer"] = "",
                    item["type"] = "select"
                }
                else if(item.configType === 3){
                    item["require"] = [],
                    item["answer"] = "",
                    item["type"] = "radio"
                }
                else if(item.configType === 4){
                    item["require"] = [],
                    item["answer"] = [],
                    item["type"] = "button"
                }
                 else if(item.configType === 5){
                    item["require"] = []
                }
                else if(item.configType === 6){
                    item["require"] = [],
                    item["answer"] = "",
                    item["type"] = "upload"
                }
                else if(item.configType === 7){
                    item["require"] = [],
                    item["answer"] = "",
                    item["type"] = "time",
                    item["showDate"] = "",
                    item["isClicked"] = false
                }
            })
            let treeData = this.toTreeData(arr);
            console.log("treeData",treeData);
            sessionStorage.setItem("QUESTION_DATA",JSON.stringify(treeData));
            this.questionList = treeData;
        }
    },
    computed:{},
    components:{
        "header-text":headerText,
        "tree-answer":treeAnswer
    },

    mounted(){
        //console.log('bbb');
        let orderId = this.$route.query.orderId;
        this.orderId = orderId;
        sessionStorage.setItem("ORDER_ID",orderId);
        //console.log(window.innerHeight);
        this.clientHeight1 = window.innerHeight -document.getElementsByClassName("fx-tips")[0].clientHeight;
        this.clientHeight = this.clientHeight1-document.getElementsByClassName("fx-btns")[0].clientHeight;
        this.getQuestionList();
    }
}
</script>

<style lang="scss">
@mixin questTitle-style{
    .questionTitle{
        // display:table-cell;
        padding:r(30) 0;
        overflow: hidden;
        .index{
            display:block;
            float:left;
            width:r(64);
            height:r(44);
            line-height:r(44);
            text-align:center;
            font-size:r(20);
            color:#fff;
            background:url("../../../static/images/num_bg.png") center no-repeat;
            background-size:r(34) r(34);
        }
        b{
            display:block;
            float:left;
            line-height:r(46);
            // height:r(46);
            font-weight:normal;
            width:r(686);
            font-size:r(32);
            color:#000;
        }
    }
}
.fx-questionnaire{
    width:r(750);
    height:100%;
    background:#fff;
    position: relative;
    overflow: hidden;
    .fx-content{
        /* 增加该属性，可以增加弹性 */
        -webkit-overflow-scrolling: touch;
        overflow: auto;
        padding-bottom:r(30);
        position: absolute;
        top:r(88);
        bottom:r(92);
        .textarea-style{
            @include questTitle-style;
            textarea{
                font-size:r(28);
                width:r(686);
                height:r(236);
                border-radius:r(10);
                border:r(2) solid #f8f8f8;
                background:#f8f8f8;
                margin:0 auto;
                display:block;
                padding:r(20);
                -webkit-appearance: none;
                outline: none;
            }
        }
        .select-style{
            @include questTitle-style;
            // div{
            //     padding:r(30) 0 r(10) 0;
            // }
            .select-choice  {
                width:r(690);
                height:r(60);
                border:none;
                border-bottom:r(1) solid #ddd;
                margin:0 auto;
                display:block;
                text-indent:r(30);
                font-size:r(30);
                appearance:none;
                -moz-appearance:none;
                -webkit-appearance: none;
                outline: none;
                background:none;
                option{
                    text-indent:r(30);
                    font-size:r(30);
                }
            }
        }
        .radio-style{
            @include questTitle-style;
            .btns{
                // width:r(570);
                // height:r(72);
                display:block;
                padding:r(0) r(20);
                &>span{
                    display:inline-block;
                    margin-left:r(90);
                }
                // margin:r(30) auto;
                &>span>.radio-item{
                    height:r(72);
                    padding:r(0) r(10);
                    min-width:r(180);
                    // width:r(240);
                    //background:#f3f3f3;
                    border:r(2) solid #ddd;
                    text-align: center;
                    line-height:r(66);
                    float:left;
                    font-size:r(34);
                    // margin-left:r(90);
                    border-radius:r(10);
                    color:#0b0b0b;
                }
                &>span:nth-child(1)>.radio-item{
                    margin-left:r(0);
                }
                &>span>.radio-item-active{
                    color:#fff;
                    background:#0084ff;
                }
            }
        }
        .button-style{
            @include questTitle-style;
            .items{
                width:100%;
                // padding:0 r(30);
                display:block;
                margin:0 auto;
                // text-align: center;
                 &>span{
                    display: inline-block;
                    height:r(72);
                    line-height:r(72);
                    margin-bottom:r(15);
                    margin-left:r(90);
                    
                    &>.button-item{
                        height:r(72);
                        line-height:r(66);
                        display:inline-block; 
                        padding:0 r(20);
                        border-radius:r(10);
                        border:r(2) solid #ddd;
                        //background: #f3f3f3;
                        font-size:r(34);
                        color:#0b0b0b;
                    }
                    &>.button-item-active{
                        color:#fff;
                        background:#0084ff;
                    }
                 }
                // &>span:nth-child(1){
                //     margin-left:r(0);
                // }
                
            }
        }
        .time-style{
            @include questTitle-style;
            &>.selectYear{
                padding:r(0) r(30);
                font-size:r(32);
                color:#000;
                text-indent:r(20);
                &>div{
                    border:r(2) solid #ccc;
                    padding:r(10) r(0);
                    border-radius:r(10);
                }
            }
        }
        .upload-style{
            @include questTitle-style;
        }

    }
    .fx-tips{
        height:r(88);
        line-height:r(88);
        font-size:r(34);
        border-bottom:1px solid #dddddd;
        color:#000;
        font-weight: bold;
        padding-left: r(27);
    }
    .fx-btns{
        width:r(750);
        height:r(92);
        position:fixed;
        bottom:0;
        border-top:1px solid #ddd;
        div{
            width:100%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
            display:block;
            text-align: center;
        }
    }
}
</style>


