<template>
<div>
	<div v-if="data.configType == '1'" class="textarea-style">
		<div class="questionTitle"><span  class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<textarea  maxlength="200" cols="30" rows="10" :placeholder="data.require" v-model="data.answer" @blur="blurEvent"></textarea>
	</div>
	<div v-if="data.configType == '6'" class="upload-style">
		<div class="questionTitle"><span class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<span>
			上传文件
		</span>
	</div>
	<div v-if="data.configType == '7'" class="time-style">
		<div class="questionTitle"><span class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<div class="selector selectYear" @click="selectYear(parentIndex,data)">
			<div v-if="!data.isClicked">选择日期</div>
			<div v-if="data.isClicked">{{data.showDate}}</div>
		</div>
	</div>
	<div v-if="data.configType == '2'" class="select-style">
		<div class="questionTitle"><span class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<select name="select-choice" class="select-choice" @change="(e)=>getCouponSelected(parentIndex,data,e)">
			<option value="">请选择</option>                               
			<option :value="coupon.content" v-for="(coupon) in data.require" :key="coupon.id">{{coupon.content}}</option>                                    
		</select>
		<div v-if="hasChildrens && hasChildrens.length>0">
			<tree-answer v-for="(item,index) in hasChildrens" :parentIndex="index" :key="item.id" :data="item"></tree-answer>
		</div>
	</div>
	<div v-if="data.configType == '3'" class="radio-style">
		<div class="questionTitle"><span class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<div class="radio-wrapper">
			<span v-for="(p,i) in data.require" :key="p.id" @click="getCouponRadio(i,data)">
				<span v-if="p.show" class="radio-item radio-item-active">{{p.content}}</span>
				<span v-if="!p.show" class="radio-item">{{p.content}}</span>
			</span>
		</div>
		<div v-if="pChildrens && pChildrens.length>0">
			<tree-answer v-for="(item,index) in pChildrens" :parentIndex="index" :key="item.id" :data="item" @change_data="change_data"></tree-answer>
		</div>
	</div>
	<div v-if="data.configType == '4'" class="button-style">
		<div class="questionTitle"><span class="index">{{data.sortIndex}}</span><b>{{data.content}}</b></div>
		<span class="items">
			<span v-for="(item,idx) in data.require" :key="item.id" @click="getCouponMulti(idx,data,item)">
				<span v-if="item.show" class="button-item button-item-active">{{item.content}}</span>
				<span v-if="!item.show" class="button-item">{{item.content}}</span>
			</span>
		</span>
		<div v-if="multChildrens && multChildrens.length>0">
			<tree-answer v-for="(item,index) in multChildrens" :parentIndex="index" :key="item.id" :data="item" @change_data="change_data"></tree-answer>
		</div>
	</div>
	<mt-datetime-picker 
    v-model="dateValue" 
    type="date" 
    ref="datePicker" 
    year-format="{value} 年" 
    month-format="{value} 月" 
    date-format="{value} 日" 
    :endDate="new Date()" 
    @confirm="handleConfirm">
    </mt-datetime-picker>
</div>
</template>

<script>
import Vue from "vue";
import treeAnswer from "@/components/treeAnswer";
	export default {
		name:"tree-answer",
		props: {
			data:Object,
			parentIndex:Number
		},
		components: {
			"tree-answer": treeAnswer,
		},
		data() {
			return {
				hasChildrens:[],
				pChildrens:[],
				multChildrens:[],
				dateValue:'',
				timeQuestionIndex:null,
				showDate:'',
			}
		},
		methods: {
			//软键盘问题
			blurEvent(){
				window.scroll(0,0);
			},
			change_data(){
				this.$emit('change_data');
			},
			getCouponMulti(index,data,item){
				console.log("item.show",item.show);
				console.log("data.answer",data.answer);
				if(item.show === undefined){
					item.show = true;
					data.answer.push(data.require[index].content);
				}
				else if(item.show === true){
					item.show = false;
					let tmpIdx = data.answer.indexOf(data.require[index].content);
					data.answer.splice(tmpIdx,1);
				}
				else if(item.show === false){
					item.show = true;
					data.answer.push(data.require[index].content);
				}
				data.show = true;
				console.log("data",data);
				console.log("item",item);
				console.log("data.require",data.require);
				let tmpArr = [];
				data.require.map((ele,m)=>{
					if(ele.show === true){
						console.log("data.require[m].require",data.require[m].require);
						data.require[m].require.map((element)=>{
							tmpArr.push(element);
						})
					}
				})
				console.log("tmpArr",tmpArr);
				this.multChildrens = tmpArr;
				this.multChildrens.map(item1=>{
					item1.show = true;
				})
				this.$forceUpdate();
				this.$emit('change_data');
			},
			getCouponRadio(index,data){
				console.log("data",data);
				data.require.map((v,i)=>{
					v.show = i===index
				})
				data.show = true;
				data.answer = data.require[index].content;
				this.pChildrens = data.require[index].require;
				this.pChildrens.map(item=>{
					item.show = true;
					console.log("item.require",item.require);
					if(item.require instanceof Array){
						item.require.map((ele)=>{
							ele.show = false;
						})
					}
				})
				console.log("this.pChildrens",this.pChildrens);
				this.$forceUpdate();
				this.$emit('change_data');
			},
			//下拉选择
			getCouponSelected(index,data,e){
				data.show = true;
				data.answer = e.target.value;
				let THAT = this;
				data.require.map((v,i)=>{
					v.show = v.content===e.target.value
					if(v.content===e.target.value){
						THAT.hasChildrens = v.require;
						THAT.hasChildrens.map(item=>{
							item.show = true;
						})
					}
				})
				THAT.$emit('change_data');
			},
			//日期选择
			handleConfirm(e){
				let showDate = e.getFullYear()+'年'+(e.getMonth() + 1)+'月'+e.getDate()+'日';
				this.data.showDate = showDate;
				this.data.isClicked = true;
				this.data.answer = e.getFullYear()+'-'+(e.getMonth() + 1>9?(e.getMonth() + 1):'0'+(e.getMonth() + 1))+'-'+(e.getDate()>9?e.getDate():'0'+e.getDate());
			},
			selectYear (index,value) {
				console.log(this.data);
				this.$refs.datePicker.open();
			},
		},
		mounted(){
			
		},
		beforeUpdate(){
			
		},
		updated(){
			
		},
		watch: {
	
		},
	};
</script>

<style lang="scss">
.radio-wrapper{
	overflow: hidden;
	.radio-item{
		height:r(72);
		padding:r(0) r(10);
		min-width:r(180);
		// width:r(240);
		//background:#f3f3f3;
		border:r(2) solid #ddd;
		text-align: center;
		line-height:r(66);
		float:left;
		font-size:r(34);
		margin-left:r(90);
		border-radius:r(10);
		color:#0b0b0b;
		margin-top:r(10);
	}
	.radio-item-active{
		color:#fff;
        background:#0084ff;
	}
}
.radio-wrapper>.radio-item:nth-child(1)>.radio-item{
	margin-left:r(0);
}
</style>



